@import url(https://fonts.googleapis.com/css2?family=Palette+Mosaic&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a{
  text-decoration: none;
  color: black;
}
a:hover{
  color: black;
  text-decoration: none;
}

body{
  background-color: #FFFFFF;
  
}
.main
{
  height: 100vh;
}

  /* width */
  ::-webkit-scrollbar {
    width: 5px;
    
  }
  
  /* Track */
  
    ::-webkit-scrollbar-track {
     
    background: #C8E1C9;
    
  }
  
  /* Handle */
  
::-webkit-scrollbar-thumb {
      background: #ccc;
}
  
    
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    
    background: #555; 
  } 


/* card style start here */
.card-container{
  margin: 40px 0;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.card{
  width: 25%;
  box-shadow: 0 0 8px #ccc;
  border-radius: 10px;
  border-top-left-radius: 50px;
  border-bottom-right-radius: 50px;
}
.card:hover{
  box-shadow: 0 0 16px #ccc;
  border:1px solid #F3C13A;
}
.card > img{
  width: 100%;
  height: 250px;
  padding: 5px;
  border-radius: 10px;
  border-top-left-radius: 50px;
}
.card-title{
  margin: 10px 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-style: italic;
}
.card-number{
  margin: 10px 5px;
  padding-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  font-size: 15px;
}
#relation{
 background-color: #F3C13A;
 border-radius: 7px;
 padding: 5px;
 float: right;
 font-weight: 400;
 font-size: 13px;
 color: #fff;
}

/* card style end here */

/*Header styel start here */
#header{
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #deebdd;
  background-image: linear-gradient(315deg, #deebdd 0%, #bbdbbe 74%);
  padding: 10px 60px;
}

.logo-wrapper{
  font-family: 'Palette Mosaic', cursive;
  font-size: 25px;
}

.menu-item-wrapper{
  display: flex;
  margin: 0 60px;
}
.menu-item{
  list-style-type: none;
  font-size: 25px;
  font-weight: 200;
  padding-left: 50px;
}

/*Header styel end here */

/*footer styel start here */
#footer{
  margin-bottom: 0;
  background-color: #deebdd;
  background-image: linear-gradient(315deg, #deebdd 0%, #bbdbbe 74%);
}
.footer-item-wrapper{
  list-style-type: none;
  display: flex;
  justify-content: center;
  padding: 20px;
}
.footer-item{
  font-weight: 200;
  padding: 0 20px;
}
/*footer styel end here */
*{
    margin:0;
    padding: 0;
    box-sizing: border-box;
}
.about-page{
    display: flex;
    margin: 50px auto;
    width: 80%;
    align-items: center;
}

.about-img-wrapper{
    width: 600px;
    height: 600px;

    display: flex;
    align-items: center;
}

.about-img-wrapper > img{
    width: 100%;
}
.about-para-wrapper{
    margin: 0 50px;
    width: 80%;
}

.about-para-wrapper > h2{
    color: #F3C13A;
    margin-bottom: 10px;
}

.about-para-wrapper > p{
    letter-spacing: 1.5px;
    font-weight: 200;
    word-spacing: 1.5px;
    margin:10px 0;
}

.team-wrapper{
    display: flex;
}

.team{
    width: 70px;
    height: 70px;
    margin-left: 10px;  
    margin: 20px;  
}
.team > img{
    width: 100%;
    border-radius: 35px;
    padding: 5px;
    border: 1px solid #F3C13A;
}
.team > p{
    font-weight: 200;
    font-size: 13px;
}
.team> img:hover{
    border: 2px solid #F3C13A;
}

#designation{
    background-color: #F3C13A;
    padding: 2px;
    border-radius: 5px;
}
*{
    padding: 0;
